    name: "Krug Grand Cuvee ",
    year: "MV",
    place: "Champagne, France",
    appellation: "Champagne",
    funfact:
      "Krug was founded in 1843 by Johann-Joseph Krug, a German immigrant from Mainz on the Rhine who learned his trade at Champagne Jacquesson for nine years before setting up his own Champagne house in Reims. Krug remained in the familys hands for several generations, with continual father-to-son handovers until 1999 when it was bought by the global luxury brands conglomerate Louis Vuitton Mot Hennessy (LVMH). Like most of the companies in the group, Krug retains a degree of autonomy and the house is still headed by Krug family member Olivier Krug, the sixth generation of Krug, who is insistent upon maintaining the houses reputation and consistent style. All the wines at Krug undergo primary fermentation in small 205-litre oak barrels from the Forest of Argonne and Central France. The barrels are all well-seasoned and organoleptically inert. Each cru is vinified separately, with no malolactic fermentation, and the two rackings are done by gravity. Krug Grande Cuvee is born from the dream of one man, Joseph Krug, to offer the very best Champagne every single year, regardless of annual variations in climate. Every year since 1843, the House of Krug has honored this dream with a new Edition of Krug Grande Cuvee: the fullest expression of Champagne. Its exceptional finesse is the result of a stay of around seven years in the cellars. The 166the edition is composed of 140 wines from 13 different harvests between 1998 and 2010. This wine is 58% from the base year of 2010, but fully 42% of the cuve is composed of reserve wines, with disgorgement done in the winter of 2017. Uniquely, the House of Krug pairs each cuvee to music, and they selected Beethovens Emperor Concerto to go with this wine.",
