    funfact:
      'Lauer\'s focus is dry and off-dry Riesling. All but one of the wines take a cask (Fass) number that indicates the vineyard parcel where thegrapescame from. Prior to the legal consolidation of vineyard names in 1971, there were many vineyard sites comparable to the "lieux-dits" of Burgundy. Lauer still ferments each of the six vineyard sites that are now part of Ayler Kupp separately so each barrel gets a number that corresponds to a parcel. These vineyards range from warm, south-facing sites near the river all the way up the hill to the cold & windy hilltop, contributing a huge range of flavors and structural components to the wines. Florian is a minimalist in their cellar in the village of Ayl. The only interventions are temperature control and a clarification prior to fermentation and battonage. Spontaneous fermentation and lees contact are the norm here. The Barrel X is a playful exception to the single site rule - a blend of parcels within the Saar from Ayler Kupp and a few parcels farmed by friends. ',
