      "The Zwettl Monastery was founded in 1074 and in 1171 the Monks of Zwettl were granted their first vineyards: Heiligenstein & Gaisbergthe oldest documented sites in the Kamptal. The estate and Castle Gobelsburg was controlled by as many as 19 different families between 1074 and 1740, and in 1786 absorbed the famous Kammern Winery and vineyards. Two hundred years later Eva and Michael Moosbrugger were granted the winemaking and viticulture contract in 1996, and with the help and guidance of Michaels mentor Willi Brndlmayer, the winery has regained its prestige and considered to be a leader in quality and innovation. Schloss Gobelsburg maintains a large number of parcels in Erste Lagen, or 1st Growth, vineyards in the Kamptal, including the deep loess soils of Renner, Grub, and Lamm planted to Grner Veltliner. The winery continues to utilize organic winegrowing and has benefited from the fact that the monks of Zwettl Monastery began these practices as early as 1958.",
