    funfact:
      "Michel Colin was the third generation in his family to growgrapesin Burgundy and when he retired in 2003, he handed the property over to his sons, Philippe and Bruno, who split the holdings between them to bottle under separate labels.  With the help of his wife, Stphanie, Bruno farms eight hectares of land, in thirty different parcels scattered over five communes, with sometimes as little as just a few rows per parcel. To farm under these circumstances is quintessentially Burgundian, where the old Napoleonic codes of inheritance (evolved from Roman law) divide property equally among offspring.  In the old family winery in the center of the village of Chassagne-Montrachet, he vinifies nineteen cuves separately, twelve of which are premier cru! He uses traditional vinification methods and ages his wines anywhere from twelve to eighteen months in barrel, though he tends to vinify each parcel in a similar fashion. Brunos style shows beautiful balance, both aromatically and texturally, while showcasing the delicate minerality and nuanced complexity of each vineyard. His holdings in Chenevottes were planted in 1979 and the wine is aged in 20-25% new French oak. ",
