    funfact:
      "Mark ryan mcneilly founded mark ryan winery in 1999 with the goal of making the best wines in washington state. Largely self-taught, mark honed the craft of winemaking through rigorous study and the welcomed advice of some of the area's most experienced producers. In the early years the winery was transient. The first vintages of long haul and dead horse were produced crushed and pressed in garages of friends and family, barrels aged in warehouses in the seattle area, and finally settling in woodinville, wa in 2003. The goal, however, remains the same. Make delicious wines that represent the vineyard from which they come. Marks wines exude tremendous power, an intensity of both fruit and oak, while remaining in balance. Mark ryan wines have become a reference point for modern washington wines. The 2018 viognier spent 6 months being aged in 49% used French oak, 27% concrete egg and 19% new French oak and 5% stainless steel. ",
