    year: "2016",
    place: "Burgundy, France",
    appellation: "Bourgogne Rouge",
    funfact:
      "Vincent Girardin was born into a family history with a long standing passion for wine. The history of Maison Vincent Girardin is relatively recent. In 1980, at the age of 19, Vincent Girardin, the son of a family of winegrowers based in Santenay since the 17th century, decided to strike out on his own, and began producing wine from two hectares of vines that he had inherited from his parents. From his earliest youth Vincent had a passion for working with vines, and great respect for the potential that they represent and was always ambitious to produce his own wine. The quality of his wines was quickly recognized by connoisseurs all over the world, and this enabled him to expand his activity, focusing primarily on the great white and red wines of the Cte de Beaune. In the years between 1980 and 1990 his passion and curiosity led him to try all of the techniques that were being employed at that time. It was only in the early 2000s with the arrival of Eric Germain as winemaker, that a profound change began to take place. They decided to concentrate on the essence of the wines: the expression of the great terroirs of Burgundy. A new purity of style was born, which has now been acknowledged by all winemakers. The Cuvee Saint-Vincent comes mostly from 45 year old vines around the Cote de Beaune. It is aged in 500L barrels (10% new)  for 10 months and blended only on fruit days according to the biodynamic calendar. ",
