    year: "2018",
    place: "Loire Valley, France",
    appellation: "Samour",
    funfact:
      "With its first recorded mention in 1063, Chateau de Breze sits on the outskirts of the small village of Breze, in the Saumur region of the Loire Valley.  At first a medieval castle, that ancient building was mostly razed in the late 1500s to start the construction of the chateau that exists today. Other than World War II, when the Germans occupied the country and Chateau de Breze, the Breze family has owned, and continues to own, the chateau. The chteau is not only a UNESCO World Heritage site, but the wines were regularly exchanged by the royals for Sauternes greatest, Chteau dYquem. When the Appellation d'Origine Contrle (AOC) of Saumur-Champigny was established in 1957, the owner of Chteau de Brz refused the inclusion of his vineyards into this red wine appellation. His claim was that his historical vineyards were the best in the area and that they should have their own AOC, Brz.  He was denied and the vineyards became part of Saumur. In 2009, the chteaus new owner, le Comte de Colbert, asked Yves Lambert and his son, Arnaud to control the winery. They accepted and immediately began converting these historical vineyards to organic after more than fifty years of chemical farming. Today, after many years of quality farming and the deft touch of Arnaud, the brilliance of the wines known centuries ago is reemerging.",
