      "The story here is one of Sommelier turned Winemaker. Jeff Lindsay-Thorsens passion for wine began within the Seattle restaurant industry. He established solid wine programs, won competitions, and achieved many accredited certifications. After traveling and working in most quality wine regions of the world, there was only one thing left that could make him a more qualified Sommelier  Winemaking. In true Garagista fashion, W.T. Vintners began as a partnership between two families in the Garage of retired Oceanographer George White. It was here that the foundation and vision of W.T. was realized  With Elegance Towards Excellence. Jeff crafts wines that resonate a sense of place and abound within the parameters of balance. He sourcesgrapesfrom some of the best vineyards in the Pacific Northwest: Les Collines, Destiny Ridge, Stoney Vine, Boushey, and his newest acquisition of Pinot Noir from Seven Springs Vineyard in Eola-Amity hills in Oregon. With an emphasis on Syrah and Rhone varietals, W.T. is also known for serious Gruner Veltliner from Underwood Mountain in Washington's Columbia Gorge. Today, you can find the Thorsen and White families and their ever expanding wine repertoire at their new winery in Woodinville.",
