    appellation: "Bugey-Cerdon",
    funfact:
      "In La Cueille, one of seven high-altitude hamlets surrounding the historic medieval town of Ponsin, Patrick and Catherine Bottex are farming the limestone slopes above the Ain River. They have been working five hectares of land since 1991 and produce only a small quantity of their beautiful, intriguing sparkling wine. As a former part of the Duchy of Burgundy, it stands to reason that several Burgundiangrape arietals have found a home herenot the least of which is Gamay. They bottle this low-alcohol wine using the mthode ancestrale, a rare technique that experts believe predates the mthode champenoise. The wine first goes through a primary fermentation in cuve, but is then bottled before all of the residual sugar has converted to alcohol. After going through a secondary fermentation in the bottle for at least two months, the wine is readyChampagnes dosage is not permitted! The resulting wine is delightfully refreshing with bright fruit, a beautiful ros hue, and a touch of sweetness.",
