    year: "2015",
    place: "Burgundy, France",
    appellation: "Gevrey-Chambertin",
    funfact:
      "No Raphet ever went to enology school; winemaking as family tradition has been passed from father to son, and now daughter, through four generations. Theres no pontificating, no philosophyjust the rhythm of a day that starts before sunrise, the years of history absorbed in each gnarled, 100-year-old vine, and the handful of barrels stashed in a dark cellar underneath the family home. Grard took over the domaine in 2005; today his daughter Marion also helps out in the cellar and fields. Grard is modest, almost shy, preferring the majesty of his old-vine Pinot Noir to do the talking. Vinification is traditional and entirely natural; fermentation takes place in vats for approximately 12 days. Then, the wines develop in oak casks for 18 months, 15% of the casks are renewed each year. Bottling is undertaken manually and no filtration is required.  This vineyard borders grand cru Latricires-Chambertin in Gevrey and grand cru Clos de la Roche in Morey-Saint-Denis, sharing similar\nsoils and exposition; in effect, it is surrounded on all sides by grand cru vineyards. ",
