    funfact:
      'Alois Kracher was born in 1959 and passed in 2007. "Luis" was one of the great visionaries of viticulture, the spearhead of the "Austrian wine miracle" of the 1990s and the motor of the worldwide renaissance of sweet wines. The entire wine world mourned the "Luis" after his untimely death. He was a skilled chemist and joined his father\'s winery at the age of 22 and subsequently created a new dessert wine style, in which fruit, finesse and balance are more important than mere sweetness. With the 1991 vintage, Alois Kracher caused a sensation internationally for the first time. Soon his name became synonymous with cult sweet wines, especially in America. Since 2007, Gerhard Kracher manages the winery that his grandfather built and made his father world famous.',
