    funfact:
      'The history of Madeira wine stretches back to the Age of Exploration, when Madeira was a frequent port of call for ships whose captains would fuel up on winefortified to prevent spoilagefor their trans-Atlantic journey. The blazing heat of the sea voyage transformed the flavor of the wines, a metamorphosis reproduced by a process called estufagem during which the wines are heated to as high as 140F and oxidated. Founded in 1996, Broadbent Madeira was inspired by Bartholomew Broadbents father, Michael Broadbent, who called Madeira his desert island wine, and who played a crucial role along with his wife, Daphne, in sourcing the wines and establishing the blends. By definition, Malmsey is the sweetest style of Madeira. Composed of Malvasia, from which the very British "Malmsey" nickname is derived. Michael Broadbent, one of the most famous tasters alive today, is responsible for blending this wine, and clearly he is someone who prefers a jolt of acidity in his madeiras, even the typically unctuous, sweet Malmsey.',
