      "Thibaud Clerget is the 28th generation of his family to make wines in Volnay and Pommard which means the Clerget family has been in Burgundy since 1268! The story of Domaine Yvon Clerget is one of rebirth. In 2009, Thibauds father Yvon decided to retire from winemaking. Knowing his son had an intense passion to continue the family legacy, he made the decision to sell thegrapesfrom the family holdings in the finest terroirs of Volnay and Pommard to Henri Boillot. During this time, Thibaud began studying the craft of vineyard management and winemaking from two storied names in Burgundy, Henri Boillot and Domaine Hudelot-Noellat. After this three year apprenticeship, Thibaud returned to take over the family domaine. In 2015 he produced his first wines and announced the re-emergence of Domaine Yvon Clerget. ",
