    appellation: "Meursault-Blagny",
    funfact:
      "Established by the Monks of Mzires (Cistercians), the Blagny vineyard is over a thousand years old. Even today, the site still has buildings which date back to this monastic past. There is no doubt that it has some of the most wonderful views in Burgundy. The lost hamlet of Blagny, up in the hills between Puligny-Montrachet and Meursault in the Cte d'Or in Burgundy, is home to Domaine Comtesse de Chrisey. Monks were the first inhabitants, planting vines in the 14th century. This almost magical, lost-in-time corner of the world boasts a unique microclimate, with a slightly different average temperature, exposition and soil than the rest of Burgundy. All of the de Chrisey premier cru vines are at least 60 years old and they encircle their ancient cellar in the Hameau de Blagny. The Meursault-Blagny 1er Cru La Genelotte is from a vineyard just north of Blagny, on the Meursault side of course, high on the slope above the village. The wine is a powerful, classic, long-lived Meursault. The Genelotte vineyard is also a monopole meaning Laurent and Hlne are sole proprietors. ",
