    name: "Auguste Clape (August Klahp)",
    year: "2013",
    place: "Northern Rhone, FR",
    appellation: "Cornas",
    funfact:
      "In the world of wine, there are many good winegrowers however, Auguste Clape will go down in history as one of the greats. A proud and uncompromising pioneer of fine winemaking in the Northern Rhne, his Syrahs from the cru of Cornas have earned their place among the most celebrated wines of France. The Clapes have been vignerons for many generations, but the infamous grower strikes of 1906 and 1907 forced Auguste's grandfather out of the Languedoc and into the Northern Rhne to start anew from practically nothing. The Clapes rebuilt their fortunes, terrace by terrace, along the steep, western slopes of the Rhne River. For many years, the majority of growers in Cornas sold their fruit to ngociants. Auguste was the first to bottle his own wine, which eventually paved the way for such contemporary superstars as Thierry Allemand. Without pretense or fanfare, Auguste, the former mayor of Cornas, was a stately picture of grace and magnanimitya no-nonsense wise man who never rested on his laurels and sought to better himself and his wines each year until his passing in 2018 at the age of 93. Today, his son, Pierre-Marie, and grandson, Olivier, carry on his legacy with honor and integrity. This wine is selection of the domaines oldest vines and is aged 22 months in large foudres and was not destemmed. ",
