    funfact:
      "Domaine du Pegau is an old name in Chateauneuf du Pape. The Feraud family is able to trace their roots all the way back to the middle of the 17th century in the Southern Rhone Valley. That is when the ancestors of the Feraud family first plantedgrape ines next to their olive trees and other assorted fruit trees in Chateauneuf du Pape. The great, grandparents of Laurence Feraud were responsible for entering the family in the wine business with their initial purchases of vines. After their death, the vineyards were divided between their 4 children. It was Paul Feraud, the father of Laurence Feraud that brought Domaine Pegau into the modern age when he began to bottle his own wine from the production of a small 5 hectare parcel of vines that he inherited in Chateauneuf du Pape. In 1987 Domaine du Pegau was formed as we know it today, when Laurence Feraud returned from her winemaking studies and she teamed up with her father Paul to create the winery. Complementing each other they have conserved the authenticity and quality of their Chateauneuf-du-Pape whilst bringing it to the attention of wine lovers around the world.",
