    appellation: "Margaux",
    funfact:
      "In 1710, Chateau Kirwan was bought by Sir John Collingwood. Collingwood was a popular negociant in the Bordeaux wine trade. The daughter of Collingwood married Mark Kirwan. At that point in time, both she and the Margaux property took on a new name. It was while Mark Kirwan was in charge of the estate that the vineyards were increased. Mark Kirwan was also responsible for the building the chateau that is in use today. Chateau Kirwan was one of the few Medoc properties that Thomas Jefferson, the Ambassador to France and future third President of the United States visited on his Bordeaux wine tasting trip. We know this based on the personal records and diaries of Thomas Jefferson, written in 1780. After a long period when Chateau Kirwan was not known for producing the quality of wine the Left Bank estate was able to produce, the management hired Michel Rolland to consult the estate starting in 1991. Michel Rolland was replaced in 2006 at Chateau Kirwan. Jean Henri Schyler was in charge of managing the chateau until he passed away, November, 2016 at the age of 85. It is expected that the three children, Yann Schyler, Sophie Schyler and Nathaile Schyler, will all help manage the property going forward.",
