    appellation: "Valtellina",
    funfact:
      "This is family of winegrowers and, for over 150 years, have been passionately devoted to the cultivation of Nebbiolo. In 1984, Arturo Pelizzatti Perego decided to resurrect his family's winery under the moniker Ar. Pe. Pe. His father and grandfather had both run their family winery in Valtellina but sold it in the 1970s. Today, Arturo's three children, Isabella, Emanuele and Guido continue their fathers work as the family's fifth generation of growers. For 5 generations the family has always waited for the time it takes the right waiting time and it is this that sets the pace of all the work. Rocce Rosse describes the red rocks that dominate this high altitude vineyard and the wine harkens to some of the great, old-school Gran Reserva bottlings of Rioja: Hand-harvested Nebbiolo, typically picked in mid-October, is fermented in 50-hectoliter oak vats known as tini, then aged in similarly large, used oak vats for four years. After the wood aging, Rocce Rosse is then rested in cement vats for a time and then further aged in bottle until Isabella and company deem it ready for release.",
