    funfact:
      "Established in 1987, Domaine Drouhin Oregon is owned by the Drouhin Family of Burgundy (Maison Joseph Drouhin). Fourth-generation winemaker Vronique Drouhin-Boss produces wines of distinction, balance, elegance and longevity. Philippe Drouhin, who oversees the family vineyards in Burgundy and Oregon, is responsible for the 225-acre estate. Today, 130 acres are under vine and certified sustainable by L.I.V.E. The familys principled approach to viticulture and winemaking, and long experience with Pinot Noir and Chardonnay, have earned an international reputation for excellence. The Louise cuve is always special. In each vintage, Vronique focuses on a few favorite barrels that, once nurtured and artfuly blended, will become Louise. Seamless, complex, layered and deeply pleasing, it is a rare and decidedly wonderful wine. The Pinot Noir is handpicked and hand-sorted, de-stemmed, and then allowed to ferment at its own pace. Typically in barrel for 15 months, Louise maintains its sense of place and vintage character through a gentle levage, and the use of only 20% new French oak. ",
