    appellation: "Pomerol",
    funfact:
      "Chateau Clinet is one of the oldest vineyards in Pomerol. Old maps unearthed by the estate show that vines were planted in 1785. From there, the vineyard became the property of the Constant Family, before it passed to the Arnaud family. The Arnauds had good taste in wine. At the same time, they were also owners of the famed Bordeaux wine property, Petrus! Following the Arnauds involvement, from 1900 to 1991, Chateau Clinet hired the managing director Jean Michel Arcaute in the late 1970s and Chateau Clinet began producing some of the best wine in their history within a few short years. He brought in Michel Rolland as the consultant and they set out to make wine that challenged the best estates of Pomerol. They increased the amount of new, French oak barrels the wine was aging in, as well as increasing the time the wine spent in oak. Chateau Clinet is situated on great terroir. Their vineyards are located at one of the highest points on the Pomerol Plateau. Their closest neighbors are only a stones throw away, Chateau l'Eglise Clinet, Chateau Clos l'Eglise, and Chateau Trotanoy. They also have vines with more gravel close to Lafleur and Petrus.",
