    place: "Washington USA",
    appellation: "Walla Walla",
    funfact:
      "The story began near the village of Charly-sur-Marne in the Champagne region of France, where a very young Christophe Baron walked the family vineyard with his father and grandfather. He was the youngest of the centuries-old Champagne house. Like generations of fathers and sons before, it was in his blood to be a wine grower and creatora true vigneron. Its a title you're born with, not something you become or learn in school, An unexpected internship at a winery brought Christophe to the Walla Walla Valley for the first time in 1993. After one year, he traveled the world gaining experience in Australia, New Zealand, Romania and Oregon. He intended to buy some land and start a vineyard from scratch, but all those plans came to an abrupt halt on an April morning in 1996. Christophe had returned to Walla Walla for a strictly social visit, and was wandering the countryside with a friend. As they drove near the Oregon/Washington border, he spied an open field littered with acres of softball-sized stones. Plans to move to the Willamette Valley were quickly discarded, and Christophe resolved to buy the property and plant a vineyard. He saw something different with enormous potential. The terroir reminded him of the cobblestones of the southern Rhone valley and Chteuneuf-du-Pape in his native France. I almost fell on my derrire when I saw those stones, he says. And I've been living the dream ever since. ",
