      "In 1982, after a six year search, Sir Peter Michael and Lady Michael purchased 630 acres of volcanic ridges on the western face of Mount St. Helena in Knights Valley. Drawing inspiration from the great wineries of Burgundy and Bordeaux, the family hired like-minded viticulturists and winemakers to help them create terroir-driven wines that would stand the test of time. Les Pavots is Peter Michaels flagship blend of red Bordeaux varietals from our Knights Valley Estate. While the blend varies from vintage to vintage, typically it is two thirds Cabernet Sauvignon with roughly equal parts Cabernet Franc and Merlot and a touch of Petit Verdot. Les Pavots captures an elegant refined expression of the terroir embracing the full spectrum of Cabernet family varietal expression with its trademark touch of herbes de provence. The name translates to The Poppies and is named after the California poppies that grow wild on our estate and are the inspiration for the winerys logo.",
