    funfact:
      "A local register from 1680 notes the existence of two brothers, Dominique et Guilhem Armailhacq, who own parcels of land in Pauillac. Another register mentions in 1750 a certain Dominique Armailhac, who has planted with vines the family estate, taking advantage of the planting frenzy then sweeping through the Mdoc at the time. Then, Chteau Mouton dArmailhacq is classified as a Fifth Growth in the famous classification drawn up by Bordeaux wine brokers 1855.  Baron Philippe de Rothschild becomes a minority shareholder in 1933. From 1956 to 1988, the wine was called Mouton Baron Philippe, then Mouton Baronne Philippe. A Fifth Growth of acknowledged quality, its original identity as Chteau dArmailhac was fully restored in 1989. 2010 was a dry but relatively cool year, with no summer heatwave. These factors  dry conditions without drought, an exceptional amount of sunshine, cool nights and rainfall that arrived at just the right time  give the 2010 vintage a remarkable identity. A splendid sequel to 2009, it is sure to become an outstanding year in the long history of fine Bordeaux wine.",
